import React, { useEffect, useState } from 'react';
import './Spacex.css';
import { useInterval } from '../customHooks/customeHooks';

const Spacex = () => {

    const [upComingLaunches, setUpComingLaunches] = useState([]);
    const [CountDown, setCountDown] = useState("")
    const CountDownLaunches = (launches = upComingLaunches) => {
        if (launches.length > 0) {
            let nextLunc = new Date(launches[0].launch_date_local);
            let launche = Date.parse(nextLunc) - Date.parse(new Date());
            let seconds = Math.floor((launche / 1000) % 60);
            let minutes = Math.floor((launche / 1000 / 60) % 60);
            let hours = Math.floor((launche / (1000 * 60 * 60)) % 24);
            let days = Math.floor(launche / (1000 * 60 * 60 * 24));
            setCountDown("T" + "- " + days + " : " + hours + " : " + minutes + " : " + seconds);
        }
    }
    useEffect(() => {
        // window.open("https://www.google.com/maps/search/?api=1&query=28.626486173436586,-80.62047030219247&query_place_id=ChIJZer0yrO74IgRbTR4IzfGf1s");
        // window.open("https://www.google.com/maps/dir/?api=1&destination=28.626486173436586,-80.62047030219247");
        fetch('https://api.spacexdata.com/v3/launches/upcoming')
            .then(res => res.json())
            .then(data => {
                setUpComingLaunches(data)
                let nextLunc = new Date(data[0].launch_date_local);
                let launche = Date.parse(nextLunc) - Date.parse(new Date());
                let seconds = Math.floor((launche / 1000) % 60);
                let minutes = Math.floor((launche / 1000 / 60) % 60);
                let hours = Math.floor((launche / (1000 * 60 * 60)) % 24);
                let days = Math.floor(launche / (1000 * 60 * 60 * 24));
                setCountDown(days + " : " + hours + " : " + minutes + " : " + seconds);
            })
    }, [])
    useInterval(() => {
        CountDownLaunches();

    }, 1000);
    const OpenGoogleMaps = (e) => {
        if (navigator.userAgent.match(/webOS/i)
            || navigator.userAgent.match(/iPhone/i)
            || navigator.userAgent.match(/iPad/i)
            || navigator.userAgent.match(/iPod/i)
            || navigator.userAgent.match(/BlackBerry/i)
            || navigator.userAgent.match(/Windows Phone/i))
            window.open("maps://www.google.com/maps/search/?api=1&query=28.626486173436586,-80.62047030219247&query_place_id=ChIJZer0yrO74IgRbTR4IzfGf1s");
        else if (navigator.userAgent.match(/Android/i))
        // window.open("geo:28.626486173436586,-80.62047030219247?z=3");
            // window.open("geo:28.626486173436586,-80.62047030219247?z=3");
           
        window.open("https://www.google.com/maps/search/?api=1&query=28.626486173436586,-80.62047030219247&query_place_id=ChIJZer0yrO74IgRbTR4IzfGf1s");
        else
            window.open("https://www.google.com/maps/search/?api=1&query=28.626486173436586,-80.62047030219247&query_place_id=ChIJZer0yrO74IgRbTR4IzfGf1s");
    }
    return (
        <div className="CountDown-warrper">
            <button onClick={(e) => OpenGoogleMaps(e)}>ddd</button>
            {upComingLaunches.length > 0 ?
                <div className="CountDown-card">
                    <div className="grid-badges">
                        <div className="MissionName">mission name:<span className="data-of-mission">{upComingLaunches[0].mission_name}</span></div>
                        <div className="FlightNumber">flight number:<span className="data-of-mission">{upComingLaunches[0].flight_number}</span></div>
                        <div className="badge-flex"><img className="badge" src={upComingLaunches[0].links["mission_patch"]} /></div>
                    </div>
                    <div className="span-warrper">
                        <span>{CountDown}</span>
                    </div>
                </div>
                : null}
        </div>
    )
}
export default Spacex;